import { mapMutations } from "vuex";
export default {
  computed: {
    todos() {
      return this.$store.state.todos.list;
    }
  },

  methods: {
    addTodo(e) {
      this.$store.commit("todos/add", e.target.value);
    },
    ...mapMutations({
      toggle: "todos/toggle"
    })
  }
};
